@charset 'utf-8';

@font-face {
  font-family: 'Inconsolata';
  src: url('../Inconsolata-VariableFont_wdth,wght.ttf'); }
$font: 'Inconsolata', sans-serif;


$linewidth: 769px;
$radius: 2px;
$gap: 20px;

$primary:      hsl(100, 27%, 50%);
$black:        hsl(0, 0%, 20%);
$grey-dark:    hsl(0, 0%, 29%);
$grey:         hsl(0, 0%, 48%);
$grey-light:   hsl(0, 0%, 71%);
$white:        hsl(0, 0%, 97%);

@mixin desktop {
  @media screen and (min-width: $linewidth) {
    @content; } }

html {
  color: $black;
  background: $white; }

body {
  font-family: $font;
  font-variation-settings: "wght" 600, "wdth" 100;
  font-size: 1rem;
  line-height: 1.5;
  hyphens: auto;
  margin: 0; }

@import 'text';
@import 'blocks';
@import 'highlight';
